<template>
    <header :class="{'header-custo': true, 'background-1':typeMenu, 'background-2':!typeMenu }" >
        <nav class="navbar elevation-8">
            <div class="container text-center">
                <v-row cols="12" align="center">
                    <v-col v-ripple>
                        <a style="color: #fff; text-decoration: none;" href="/#/">
                            <span class="logo">Abismo Studios</span>
                        </a>
                    </v-col>
                    <v-col>
                        <div class="menuLinks">
                            <a v-for="(item, i) in menu" :key="i" :href="'#'+item.href" :class="{'active':($route.path == item.href)}">{{ item.title }}</a>
                        </div>
                    </v-col>
                </v-row>
            </div>
        </nav>
        <div v-show="$route.path != '/'"  class="space"></div>
    </header>
</template>

<script>
export default {
    name: "Navbar",
    mounted()
    {
        console.log(this.$route.path);
    },
    data()
    {
        return {
            menu: [
                {
                    title: 'Inicio',
                    href: '/',
                },
                {
                    title: 'Projetos',
                    href: '/project',
                },
                {
                    title: 'Equipe',
                    href: '/team',
                },
                {
                    title: 'Contato',
                    href: '/contact',
                }
            ]
        }
    },
    computed: {
        typeMenu()
        {
            return (this.$route.path != '/') ? true : false;
        }
    }
}
</script>

<style>

.header-custo {
    width: 100%;
    z-index: 9999;
    position: relative;
}

.header-custo > nav.navbar {
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
}

.header-custo.background-1 > nav.navbar {
    background: var(--secondary)!important;
}

.header-custo.background-2 > nav.navbar {
    background: rgba(0, 0, 0, 0.4)!important;
}


.header-custo > nav.navbar > div.container {
    height: 100%;
}

.header-custo > nav.navbar > div.container > div.row {
    height: 100%;
}

.header-custo > div.space {
    height: 64px;
    width: 100%;
    display: block;
}

span.logo {
    display: block;
    width: 100%;
    font-size: 2em;
    font-weight: bold;
}

.menuLinks {
    padding-top: 0.3em;
}

.menuLinks > a {
    color: #999!important;
    font-size: 1.06em;
    margin-right: 1.5em;
    text-decoration: none;
    transition: 0.4s;
}
.menuLinks > a:last-child {
    margin-right: 0px;
}

.menuLinks > a:hover {
    transition: 0.4s;
    color: #fff!important;
}

.menuLinks > a.active {
    color: #fff!important;
}

</style>